<template>
  <form class="mainForm" @submit.prevent="submitHandler">
    <div class="mainDiv">
      <h1 class="hh">Регистрация</h1>
      <div class="formOne" name="naimData">
        <h2>Основные данные:</h2>
        <div class="input-fields" name="lastName">
          <label class="form__label">Фамилия:*</label>
          <input
            id="lastName"
            v-model="lastName"
            :class="{ error: $v.lastName.$error }"
            @blur="$v.lastName.$touch()"
          />
          <div class="warningDiv" v-if="$v.lastName.$error">
            Это поле обязательно
          </div>
        </div>
        <div class="input-fields" name="name">
          <label class="form__label">Имя:*</label>
          <input
            id="name"
            v-model="name"
            :class="{ error: $v.name.$error }"
            @blur="$v.name.$touch()"
          />
          <div class="warningDiv" v-if="$v.name.$error">
            Это поле обязательно
          </div>
        </div>
        <div class="input-fields" name="patronymic">
          <label class="form__label">Отчество:</label>
          <input id="patronymic" v-model="patronymic" />
        </div>
        <div class="input-fields" name="bDate">
          <label class="form__label">Дата рождения:*</label>
          <input
            id="date"
            type="date"
            v-model="date"
            :class="{ error: $v.date.$error }"
            @blur="$v.date.$touch()"
          />
          <div class="warningDiv" v-if="$v.date.$error">
            Это поле обязательно
          </div>
        </div>
        <div class="input-fields" name="tel">
          <label class="form__label">Номер телефона:*</label>
          <input
            id="tel"
            type="number"
            v-model="tel"
            placeholder="+7"
            :class="{ error: $v.tel.$error }"
            @blur="$v.tel.$touch()"
          />
          <div class="warningDiv" v-if="$v.tel.$dirty && !$v.tel.required">
            Это поле обязательно
          </div>
          <div
            class="warningDiv"
            v-if="
              ($v.tel.$dirty && !$v.tel.isPhoneFirstNumberSeven) ||
                ($v.tel.$dirty && !$v.tel.maxLength) ||
                ($v.tel.$dirty && !$v.tel.minLength)
            "
          >
            Номер должен начинаться с "7" и иметь 11-ти значный формат
          </div>
          <div class="warningDiv" v-if="$v.tel.$dirty && !$v.tel.isPhone">
            Укажите номер в числовом формате
          </div>
        </div>
        <div class="input-fields" name="gender">
          <label class="form__label">Пол:</label>
          <div class="radioInput">
            <input id="male" type="radio" value="male" v-model="gender" />
            <label>Мужчина</label>
            <input
              id="female"
              type="radio"
              name="gender"
              value="female"
              v-model="gender"
            />
            <label>Женщина</label>
          </div>
        </div>
        <div class="input-fields problem_input_fields" name="clientGroups">
          <label class="form__label">Группа клиентов:*</label>
          <select
            id="clientGroups"
            v-model="CurrentClientGroup"
            multiple
            class="multiSelect"
            :class="{ error: $v.CurrentClientGroup.$error }"
            @blur="$v.clientGroups.$touch()"
          >
            <option
              v-for="(group, index) in clientGroups"
              :value="group.value"
              :key="index"
              >{{ group.label }}</option
            >
          </select>
          <div class="warningDiv" v-if="$v.CurrentClientGroup.$error">
            Это поле обязательно
          </div>
        </div>
        <div class="input-fields" name="doctor">
          <label class="form__label">Лечащий врач:</label>
          <select id="doctor" v-model="doctor" class="selectD">
            <option
              v-for="(doctor, index) in doctorList"
              :value="doctor.value"
              :key="index"
              >{{ doctor.label }}</option
            >
          </select>
        </div>
        <div class="input-fields-checkbox" name="SMS">
          <label>Не отправлять СМС:</label>
          <input id="SMS" type="checkbox" class="checkBox" v-model="SMS" />
        </div>
      </div>
      <div class="formOne" name="adress">
        <h2>Адрес:</h2>
        <div class="input-fields" name="index">
          <label class="form__label">Индекс:</label>
          <input id="index" type="number" v-model="patronymic" />
        </div>
        <div class="input-fields" name="country">
          <label class="form__label">Страна:</label>
          <input id="country" v-model="country" />
        </div>
        <div class="input-fields" name="area">
          <label class="form__label">Область:</label>
          <input id="area" v-model="area" />
        </div>
        <div class="input-fields" name="city">
          <label class="form__label">Город:*</label>
          <input
            id="city"
            v-model="city"
            :class="{ error: $v.city.$error }"
            @blur="$v.city.$touch()"
          />
          <div class="warningDiv" v-if="$v.city.$error">
            Это поле обязательно
          </div>
        </div>
        <div class="input-fields" name="street">
          <label class="form__label">Улица:</label>
          <input id="street" v-model="street" />
        </div>
        <div class="input-fields" name="home">
          <label class="form__label">Дом:</label>
          <input id="home" type="number" v-model="home" />
        </div>
      </div>
      <div class="formOne" name="pasport">
        <h2>Паспорт:</h2>
        <div class="input-fields" name="docymentType">
          <label class="form__label">Тип документа:*</label>
          <select
            id="docymentType"
            v-model="docymentType"
            class="selectD"
            :class="{ error: $v.docymentType.$error }"
            @blur="$v.docymentType.$touch()"
          >
            <option
              v-for="(docymentType, index) in docymentTypes"
              :value="docymentType.value"
              :key="index"
              >{{ docymentType.label }}</option
            >
          </select>
          <div class="warningDiv" v-if="$v.docymentType.$error">
            Это поле обязательно
          </div>
        </div>
        <div class="input-fields" name="series">
          <label class="form__label">Серия:</label>
          <input id="series" type="number" v-model="series" />
        </div>
        <div class="input-fields" name="docNumber">
          <label class="form__label">Номер:</label>
          <input id="docNumber" type="number" v-model="docNumber" />
        </div>
        <div class="input-fields" name="issuedBy">
          <label class="form__label">Кем выдан:</label>
          <input id="issuedBy" v-model="issuedBy" />
        </div>
        <div class="input-fields" name="issueDate">
          <label class="form__label">Дата выдачи:*</label>
          <input
            id="issueDate"
            v-model="issueDate"
            :class="{ error: $v.issueDate.$error }"
            @blur="$v.issueDate.$touch()"
            type="date"
          />
          <div class="warningDiv" v-if="$v.issueDate.$error">
            Это поле обязательно
          </div>
        </div>
        <div class="notificationDiv">
          "*" отмечены поля обязательные для заполнения
        </div>
      </div>
      <div
        class="formOne NotificationFormDiv"
        :class="{
          errorNoDi: submitStatus === 'ERROR',
          successNoDi: submitStatus === 'OK',
        }"
        v-if="submitStatus"
        name="notification"
      >
        <p class="typoOk" v-if="submitStatus === 'OK'">
          Благодарим вас, {{ this.$v.name.$model }} {{ this._data.patronymic }},
          новый клиент успешно создан!
        </p>
        <p class="typoBad" v-if="submitStatus === 'ERROR'">
          Пожалуйста, проверьте форму ещё раз и заполните все обязательные поля
          корректно
        </p>
        <p class="typoSend" v-if="submitStatus === 'PENDING'">Отправка...</p>
      </div>
      <button
        class="submitButton"
        type="submit"
        :disabled="submitStatus === 'PENDING'"
      >
        Зарегистрироваться
      </button>
    </div>
  </form>
</template>

<script>
// @ is an alias to /src
import { required, maxLength, minLength } from "vuelidate/lib/validators";
import "./TestForm.sass";

const isPhone = (value) => /^\+?[0-9]+$/.test(value);
const isPhoneFirstNumberSeven = (value) => /^\+?[7]{1}\+?[0-9]+$/.test(value);

export default {
  name: "Form",

  data: () => ({
    name: "",
    lastName: "",
    patronymic: "",
    date: "",
    tel: "",
    gender: "male",
    CurrentClientGroup: [],
    doctor: "",
    doctorList: [
      {
        label: "Иванов",
        value: "Ivanov",
      },
      {
        label: "Захаров",
        value: "Zaharov",
      },
      {
        label: "Чернышева",
        value: "Chernysheva",
      },
    ],
    clientGroups: [
      {
        label: "VIP",
        value: "VIP",
      },
      {
        label: "Проблемные",
        value: "Problem",
      },
      {
        label: "ОМС",
        value: "OMS",
      },
    ],
    SMS: false,
    index: "",
    country: "",
    area: "",
    city: "",
    street: "",
    home: "",
    docymentType: "",
    docymentTypes: [
      {
        label: "Паспорт",
        value: "Pasport",
      },
      {
        label: "Свидетельство о рождении",
        value: "birthCertificate",
      },
      {
        label: "Вод. удостоверение",
        value: "driversLicense",
      },
    ],
    series: "",
    docNumber: "",
    issuedBy: "",
    issueDate: "",
    submitStatus: null,
  }),
  validations: {
    name: { required },
    lastName: { required },
    date: { required },
    tel: {
      required,
      isPhone,
      maxLength: maxLength(11),
      minLength: minLength(11),
      isPhoneFirstNumberSeven,
    },
    CurrentClientGroup: { required },
    city: { required },
    docymentType: { required },
    issueDate: { required },
  },
  methods: {
    submitHandler() {
      console.log("submit!");
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.submitStatus = "ERROR";
      } else {
        // do your submit logic here
        this.submitStatus = "PENDING";
        setTimeout(() => {
          this.submitStatus = "OK";
        }, 500);
      }
    },
  },
};
</script>
